import { Button, Space } from 'antd'
import React, { useRef } from 'react'
import Child from './Child'
function Father() {

  const ref = useRef()

  return (
    <div>
      父组件
      <Space>
        <Button onClick={() => {
          const { current } = ref
          current.open()
        }}>打开</Button>
        <Button>关闭</Button>
      </Space>
      <Child ref={ref} />
    </div>
  )
}

export default Father
